import React, { Component } from "react";
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from "antd-mobile";

import { Link } from "react-router-dom";

import styles from "./index.module.css";
import { login } from "../../utils/api/user";
import { setLocal, ZFW_TOKEN } from "../../utils";
import { withFormik } from "formik";
import * as yup from "yup";

// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/;
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/;

class Login extends Component {
  // 
  // state = {
  //   username: "",
  //   password: "",
  // };

  // 
  // handlerChange = (e) => {
  //   // console.log(e.target);

  //   // 
  //   this.setState({
  //     [e.target.name]: e.target.value,
  //   });
  // };

  // 
  login = async (e) => {
    // 
    e.preventDefault();
  };

  render() {
    const {
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit,
    } = this.props;
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light"></NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="username"
                value={values.username}
                onChange={handleChange}
                placeholder=""
              />
            </div>
            {/* 58 */}
            {errors.username && (
              <div className={styles.error}>{errors.username}</div>
            )}
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="password"
                type="password"
                value={values.password}
                onChange={handleChange}
                placeholder=""
              />
            </div>
            {/* 512 */}
            {errors.password && (
              <div className={styles.error}>{errors.password}</div>
            )}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    );
  }
}

const NewLogin = withFormik({
  // 
  mapPropsToValues: () => ({ username: "", password: "" }),

  // Custom sync validation
  // validate: values => {
  //   const errors = {};

  //   if (!values.name) {
  //     errors.name = 'Required';
  //   }

  //   return errors;
  // },

  // 
  validationSchema: yup.object().shape({
    username: yup
      .string()
      .required("")
      .matches(REG_UNAME, "58"),
    password: yup
      .string()
      .required("")
      .matches(REG_PWD, "512"),
  }),

  handleSubmit: async (values, { props: { history } }) => {
    // 
    const { username, password } = values;
    //  console.log(username, password);
    const { status, data, description } = await login({ username, password });
    console.log(status, data, description);

    if (status === 200) {
      Toast.success(description, 2);
      // token
      setLocal(ZFW_TOKEN, data.token);
      // 
      history.push("/home/profile");
    } else {
      Toast.fail(description, 2);
    }
  },

  displayName: "BasicForm",
})(Login);
export default NewLogin;
