// 

import { getCityInfo } from "./api/City";

const CURR_CITY = "curr_city",
  ZFW_TOKEN = "ZFW_TOKEN";
// export default CURR_CITY;
// 
// 
export function setLocal(key, val) {
  localStorage.setItem(key, val);
}
// 
export function getLocal(key) {
  return localStorage.getItem(key);
}
// 
export function delLocal(key) {
  localStorage.removeItem(key);
}

// token
// token
export function setToken(token) {
  setLocal(ZFW_TOKEN, token);
}

// token
export function delToken(token) {
  delLocal(ZFW_TOKEN);
}
// token
export function getToken(token) {
  return getLocal(ZFW_TOKEN);
}

// 
const isAuth = () => !!getToken();

// Promise=asyncawaitresolve
//  

// API
const getCityName = async () => {
  return new Promise((resolve, reject) => {
    let myCity = new window.BMap.LocalCity();
    myCity.get((res) => {
      resolve(res.name);
    });
  });
};

export async function getCurCity() {
  // 
  let curCity = JSON.parse(getLocal(CURR_CITY));
  // =
  // 
  let res = await getCityName();
  let realName = res.substr(0, 2);
  // console.log(realName);

  // if (!curCity || (curCity && realName!==curCity.label)) { 
  if (!curCity) {
    // ()
    // promise=resolve
    return new Promise(async (resove, reject) => {
      // 
      // console.log(result.name);
      const { status, data } = await getCityInfo(realName);

      // console.log(status, data);

      if (status === 200) {
        // 
        setLocal(CURR_CITY, JSON.stringify(data));

        // 
        resove(data);
      } else {
        reject("error");
      }
    });
  } else {
    // 
    return Promise.resolve(curCity);
  }
}

export { CURR_CITY, ZFW_TOKEN, isAuth };
