import React, { Component } from "react";
import { Carousel, Flex, Grid, WingBlank, SearchBar } from "antd-mobile";
import { BASE_URL } from "../../utils/axios";
import { getSwiper, getGroups, getNews } from "../../utils/api/Home";
import "./index.scss";
import Navs from "../../utils/navConfig";
import { getCurCity } from "../../utils";

class Index extends Component {
  state = {
    // 
    keyword: "",
    // 
    swiper: [],
    // 
    imgHeight: 176,
    // 
    isPlay: false,
    // 
    groups: [],
    // 
    news: [],
    // 
    curCity: {
      label: "--",
      value: "",
    },
  };

  componentDidMount() {
    this.getAllDatas();
    this.getCurCity();
  }

  // 
  getCurCity = async () => {
    const res = await getCurCity();
    // console.log(res);
    this.setState({
      curCity: res,
    });
  };

  // 
  getAllDatas = async () => {
    // const p1 = Promise.resolve(1); // promise === new Promise
    // const p2 = Promise.resolve([{ a: 1, b: 2 }]);
    try {
      let [swiper, groups, news] = await Promise.all([
        getSwiper(),
        getGroups(),
        getNews(),
      ]);
      // console.log(swiper, groups, news);
      if (
        swiper.status === 200 &&
        groups.status === 200 &&
        news.status === 200
      ) {
        this.setState(
          {
            swiper: swiper.data,
            groups: groups.data,
            news: news.data,
          },
          () => {
            this.setState({
              isPlay: true,
            });
          }
        );
      }
    } catch (error) {
      console.log(error);
    }
  };

  // 
  renderSwiper = () => {
    return (
      <Carousel
        // 
        autoplay={this.state.isPlay}
        // 
        infinite
      >
        {this.state.swiper.map((val) => (
          <a
            key={val.id}
            href="http://www.alipay.com"
            style={{
              display: "inline-block",
              width: "100%",
              height: this.state.imgHeight,
            }}
          >
            <img
              src={`${BASE_URL}${val.imgSrc}`}
              alt=""
              style={{ width: "100%", verticalAlign: "top" }}
              onLoad={() => {
                // fire window resize event to change height
                window.dispatchEvent(new Event("resize"));
                this.setState({ imgHeight: "auto" });
              }}
            />
          </a>
        ))}
      </Carousel>
    );
  };

  // 
  renderNavs = () => {
    return (
      <Flex className="nav">
        {Navs.map((item) => (
          <Flex.Item
            onClick={() => {
              this.props.history.push(item.path);
            }}
            key={item.id}
          >
            <img src={item.img} alt="" />
            <p>{item.name}</p>
          </Flex.Item>
        ))}
      </Flex>
    );
  };

  // 
  renderNews() {
    return this.state.news.map((item) => (
      <div className="news-item" key={item.id}>
        <div className="imgwrap">
          <img className="img" src={`${BASE_URL}${item.imgSrc}`} alt="" />
        </div>
        <Flex className="content" direction="column" justify="between">
          <h3 className="title">{item.title}</h3>
          <Flex className="info" justify="between">
            <span>{item.from}</span>
            <span>{item.date}</span>
          </Flex>
        </Flex>
      </div>
    ));
  }

  //
  renderGroups = () => {
    return (
      <>
        <Flex className="group-title" justify="between">
          <h3></h3>
          <span></span>
        </Flex>

        {/*  */}
        <Grid
          data={this.state.groups}
          // 
          columnNum={2}
          // 
          hasLine={false}
          // 
          square={false}
          renderItem={(item) => (
            // item
            <Flex className="grid-item" justify="between">
              <div className="desc">
                <h3>{item.title}</h3>
                <p>{item.desc}</p>
              </div>
              <img src={`${BASE_URL}${item.imgSrc}`} alt="" />
            </Flex>
          )}
        />
      </>
    );
  };

  // 
  renderTopNav = () => {
    const { push } = this.props.history;
    return (
      <Flex justify="around" className="topNav">
        <div className="searchBox">
          <div
            className="city"
            onClick={() => {
              push("/cityList");
            }}
          >
            {this.state.curCity.label}
            <i className="iconfont icon-arrow" />
          </div>
          <SearchBar
            // 
            value={this.state.keyword}
            onChange={(v) => this.setState({ keyword: v })}
            placeholder=""
          />
        </div>
        <div
          className="map"
          onClick={() => {
            push("/map");
          }}
        >
          <i key="0" className="iconfont icon-map" />
        </div>
      </Flex>
    );
  };

  render() {
    return (
      <div className="index">
        {/*  */}
        {this.renderTopNav()}

        {/*  */}
        {this.renderSwiper()}

        {/*  */}
        {this.renderNavs()}

        {/*  */}
        <div className="group">{this.renderGroups()}</div>

        {/*  */}
        <div className="news">
          <h3 className="group-title"></h3>
          <WingBlank size="md">{this.renderNews()}</WingBlank>
        </div>
      </div>
    );
  }
}
export default Index;
